@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.section-card {
    position:relative;
    width: 100%;
    display:flex;
    flex-direction:row;

    /*background:darken(rgb(2, 117, 121), 10%);*/
    background:white;
    color:rgb(0, 0, 0);
    margin: 0 0 ;
    z-index:99;
    /*padding: 7vh 0;*/
    @include mobile-only {
        flex-wrap:wrap;
        padding-bottom: 8vh;
    }

        &:nth-child(even) {
        color:darken(rgb(3, 152, 158), 1%);
        flex-direction: row-reverse;
        background:rgb(251, 251, 251);
        }

    &__image {
        width: 32vw;
        height: 40vw;
        object-fit:cover;
        filter: saturate(0.9);
        &:hover {
            filter: saturate(1.2);
        }
        @include mobile-only {
            width:100%;
            height: 90vw;
        }
    }
    &__text-container {
        padding: 1vh;
        height: 20vh;
        width: 65vw;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        align-items:flex-start;
        align-content:flex-start;
        justify-content:center;
        position:relative;
        @include mobile-only {
            height: max-content;
            width:99vw;
            padding:0;

        }
        & > * {
            width:100%;
        }
        
        & i {
            font-family: 'Font Awesome 5 Free';
            font-weight: 600;
            border-radius: 50%;
            font-size: 6vh;
            width: 33%;
            height:3vw;
            display:flex;
            align-items:center;
            justify-content:center;
            margin-bottom: 1vw;
            padding:0;
            @include mobile-only {
                font-size:10vw;
                height: 10vw;
                margin: 5vh 0;
            }
        }
    }
    &__title {
        font-weight: 200;
        font-size: 6vh;
        margin: 0;
        position:absolute;
        top: 1vh;
        z-index:99;
        background: rgba(255, 255, 255, 0.712);
        width:max-content;
        color: black;
        box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.664);
        padding:0.3vw;
        margin: 0 5vh;
        @include mobile-only {
            padding: 0;
            margin:0;
            top:0;
            font-size: 9vw;
            width: 80vw;


        }
    }
    &__subtitle {
        width: 100vw;
        font-size: 4vh;
        line-height: 6vh;
        display:flex;
        flex-direction:row;
        justify-content:space-evenly;
        align-items:center;
        text-align:center;
        margin-top: 3vh;
        font-weight: normal;
        @include mobile-only {
            width: 100vw;
            font-size:2.3vh;
            position:absolute;
            top: -12vh;
            color: rgb(0, 87, 109);
            background:rgba(255, 255, 255, 0.493);

        }
    }
    &__content {
        display:flex;
        flex-direction: column;
        flex-wrap:wrap;
        justify-content: flex-start;
        height: 30vw;
        align-items:flex-start;
        @include mobile-only {
            height: max-content;
            width: 100%;

        }

        & > * {
            margin: 1vh 0;
            font-size: 1.2vw;
            font-weight: 400;
            width: 63vw;
            margin: 2vh;
            text-align:justify;
            color:rgb(0, 89, 131);
            &:nth-child(odd) {
                color: rgb(58, 58, 58);
            }
            @include mobile-only {
                width:80%;
                padding-left: 5vw;
                font-size: 2vh;
            }
        }
    }
}