@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  font-family: 'Jost';
  color:white;

}

.home {
    height: 100vh;
    width: 100%;
  &__image {
    width: 100%;
    height: 120vh;
    object-fit: cover;
    filter: brightness(0.6);
    position:sticky;
    top:-0vh;
    left:0;
    z-index:2;
    margin-bottom: -50vh;
    @include mobile-only {
        height: 110vh;
        filter:saturate(0.3);
    }

  }
  &__activity-image {
    width: 100%;
    height: 95vh;
    object-fit: cover;
    filter: saturate(1.3);
    position:absolute;
    bottom:-40vh;
    left:0;
    z-index:0;
  }
  &__title-container {
    position: sticky;
    right: 10vh;
    bottom: 25vh;
    height: 40vh;
    width: 100vw;
    z-index: 98;
    display:flex;
    align-items:flex-end;
    padding-bottom: 2vh;
    flex-wrap:wrap;
    @include mobile-only {
      flex-direction:row;
    }
  }
  &__title {
    
    height: 29vh;
    width:100%;;
    display:flex;
    flex-wrap:wrap;
    align-items:flex-start;
    z-index:98;
    font-family: 'Cabin Sketch';
    @include mobile-only {
      align-items: center;
      flex-direction:column;
    }

    
    &__element {
        margin: 0 7vh;
        width: 100%;
        text-align:center;
        font-size: 10vh;
        color: white;
        font-weight: 200;
        text-shadow: 4px 4px 4px black;
        padding: 0;
        margin:0;
        @include mobile-only {
          font-size: 5vh;
        }

    }
  }
  &__webee {
    margin-top: -15vh;
    height: 30vh;
    width:100%;;
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    align-items:flex-end;
    z-index:98;
    object-fit:contain;
    -webkit-filter: drop-shadow(5px 5px 5px #222222);
    filter: drop-shadow(5px 5px 5px #222222);
    @include mobile-only {
      height: 20vh;
      margin-top: 5vh;
    }
}
}
