@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.activity-details {
    padding-bottom: 8vh;

    flex-wrap: wrap;
    display: flex;
    box-sizing: border-box;
    padding: 10vh 0;
    font-size: 7vh;
    position:relative;
    z-index: 99;
    height: max-content;
    background-color: rgba(59, 59, 59, 0.644);
    width: 100%;
    padding: 2vw;
    font-family: 'Cabin Sketch';
    justify-content: center;
    @include mobile-only {
        flex-direction:row;
        
    }
    &__title {
        width: 100%;
        font-size: 5vw;
        margin-bottom: 2vw;
        color:rgb(255, 255, 255);
        @include mobile-only {
            font-size: 9vw;
            background-color:rgba(0, 0, 0, 0.253);

        }
    }
    & i {
        font-size: 5vw;
        -webkit-text-stroke: 3px rgb(0, 160, 160);
        -webkit-text-fill-color: rgba(255, 255, 255, 0.924);
        text-shadow: 1px 1px 2px rgb(0, 0, 0);


        @include mobile-only {
            width: 40vw;
            height: 40vw;
            font-size: 20vw;
            
            padding: 1vh;
            box-sizing: border-box;
            border-radius: 50%;
            display:flex;
            justify-content:center;
            align-items:center;
        }
    }
    &__label {
        margin-top: 4vw;
       font-size: 2vw;
       
       @include mobile-only {
        width: 100%;
        margin-bottom: 2vh;

        width: 95vw;
        margin-top: -5vw;

        text-shadow: none;
        font-size: 7vw;
    }
    
    }
    &__container {
        width: 20%;;
        color: #ffffff;
       
        margin: 3vw;
        padding: 2vw;
        border-radius: 0.5vw;

        @include mobile-only {
            display:flex;
            justify-content:center;
            flex-wrap:wrap;
            width:80%;
            background:unset;
            


        }
    }
}