.nav {
    width: 100%;
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
    height: 100%;
    align-items:center;
    font-size: 2vh;
    &__link {
        color: rgb(255, 255, 255);
        text-decoration: none;
        font-weight:400;
        &:visited {
            color: rgb(250, 250, 250);
            text-decoration: none;
        }
        &:hover {
            color:lighten(rgb(3, 152, 158), 25%);
        }
    }
}