@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.footer {
    width: 100%;
    min-height: 50vh;
    background-color:rgba(0, 0, 0, 0.582);
    position:relative;
    z-index:99;
    font-size: 8vh;
    padding-bottom: 3vh;
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    justify-content:space-evenly;
    @include mobile-only {
        font-size: 4vw;
    }
    &__credits {
        width: max-content;
        margin-top: 1vw;
        font-size: 2vh;
        padding: 1vh;
        @include mobile-only {
            font-size: 2vw;
        }
    }
    &__icon-link {
        color:white;
        width: 33%;
        height: max-content;
        text-align:center;
        margin-top: 3vw;
        margin: 2vw 0;
        @include mobile-only {
            font-size: 20vw;
        }
    }
    &__icon-text {
        font-size: 4vh;
        width: 33%;
        @include mobile-only {
            font-size: 4vw;
        }
    }
}
